.cardDiv {
  display: flex;
  align-items: center;

  border: 1px solid var(--border-color);

  padding: 25px;
  border-radius: 7px;
  margin: 0px 5px 10px 5px;

  height: 120px;
  width: 80%;
  margin: 0;
  transition: all 0.2s ease;
}
.cardDiv:hover {
  border: 1px solid black;
}

.stateButtonActive {
  background-color: greenyellow;

  padding: 4px 15px;
  border: 1px solid var(--border-color);
  border-radius: 5px;
  margin: 0 0 0 15px;

  height: 30px;

  font-size: 16px;

  cursor: pointer;
}

.stateButtonDisabled {
  background-color: rebeccapurple;

  padding: 4px 15px;
  border: 1px solid var(--border-color);
  border-radius: 5px;
  margin: 0 0 0 15px;

  height: 30px;

  font-size: 16px;

  cursor: pointer;
}

/*//////////////////////////////////////////////////////////////////*/

.stateDiv {
  display: flex;

  border: 1px solid red;
  margin: 0 0 0 10px;
}

.switch {
  position: relative;
  display: inline-block;

  /* border: 1px solid black; */
  /* border-radius: 10px; */
  margin: 17px 0 0 5px;

  width: 32px;
  height: 18px;
}

.checkInput {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;

  border-radius: 25px;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  transition: 0.8s ease-in-out;

  background-color: rgb(88, 57, 88);
}

.slider::before {
  content: "";

  position: absolute;
  left: 2px;
  bottom: 2px;

  border-radius: 100px;

  height: 14px;
  width: 14px;

  background-color: white;
  transition: 0.6s ease-out;
}

.checkInput:checked + .slider::before {
  transform: translateX(14px);
}

.checkInput:checked + .slider {
  background-color: rgb(156, 228, 156);
}
